<template>
  <div>
    <div>
      <button @click="randommonster(0,4)" class="btn btn-info">กดสุ่ม Monster</button>
    </div>
  </div>
</template>

<script>
export default {
  data: function () {
    return {
      num1: 0,
    };
  },
  methods: {
    randommonster: function (min, max) {
      this.num1 = Math.max(Math.floor(Math.random() * max) + 1, min);
      this.$emit("bb", this.num1);
    },
  },
};
</script>

