<template>
  <div id="app" style="background-color:black;">
    <HelloWorld msg="Welcome to The one punch man game" />
    <img src="./assets/logo.jpg" />
    <div class="row">
      <div class="col">
        <HelloWorld msg="select the hero" />
        <p></p>
        <Reset />
        <p></p>
      </div>
    </div>

    <div class="row">
      <div class="col-4">
        <Heropic @cc="ranH" />
        Name = {{player[total1].name}}/Hp = {{player[total1].hp}}
        <img
          :src="player[total1].image"
          width="300"
          height="200"
        />
      </div>

      <div class="col-4">
        <Monsterpic @bb="ranM" />
        Name = {{monster[total2].name}}/Hp = {{monster[total2].hp}}
        <img
          :src="monster[total2].image"
          width="300"
          height="200"
        />
      </div>

      <div class="col-4">
        <Attackbut @N="sumH" @MN="sumM" @S="sumHS" @MS="sumMS" />
        <h2>Hero Hp = {{player[total1].hp}}</h2>
        <h2>Monster Hp = {{monster[total2].hp}}</h2>
        <h2>{{Result}}</h2>
        <h2>{{Result2}}</h2>
      </div>
    </div>
  </div>
</template>

<script>
import HelloWorld from "./components/HelloWorld.vue";
import Heropic from "./components/Heropic.vue";
import Monsterpic from "./components/Monsterpic.vue";
import Attackbut from "./components/Attackbut.vue";
import Reset from "./components/Reset.vue";

export default {
  name: "App",
  components: {
    HelloWorld,
    Heropic,
    Monsterpic,
    Attackbut,
    Reset,
  },
  data: function () {
    return {
      name: "",
      hp: 0,
      total1: 0,
      total2: 0,
      Result: "",
      Result2: "",

      player: [
        { image: require("./assets/logoM.jpg") },
        { name: "Saitama", hp: 300, image: require("./assets/h1.jpg") },
        { name: "Tasumaki", hp: 200, image: require("./assets/h2.jpg") },
        { name: "Janos", hp: 125, image: require("./assets/h3.jpg") },
        { name: "Mumen rider", hp: 35, image: require("./assets/h4.jpg") },
      ],
      monster: [
        { image: require("./assets/logoM.jpg") },
        { name: "Giantfukakao", hp: 120, image: require("./assets/m1.png") },
        { name: "Kingofdeepsea", hp: 150, image: require("./assets/m2.png") },
        { name: "Carpkung", hp: 35, image: require("./assets/m3.jpg") },
        { name: "Boros", hp: 225, image: require("./assets/m4.jpg") },
      ],
    };
  },
  methods: {
    ranH(value1) {
      this.total1 = value1;
    },
    ranM(value2) {
      this.total2 = value2;
    },
    sumH(Attack1) {
      this.player[this.total1].hp = this.player[this.total1].hp - Attack1;
      if (this.player[this.total1].hp <= 0) {
        this.Result = "Monster win";
        this.Result2 = "Hero Death";
      }
    },
    sumM(Attack2) {
      this.monster[this.total2].hp = this.monster[this.total2].hp - Attack2;
      if (this.monster[this.total2].hp <= 0) {
        this.Result = "Monster Death";
        this.Result2 = "Hero win";
      }
    },
    sumHS(Attack3) {
      this.player[this.total1].hp = this.player[this.total1].hp - Attack3;
      if (this.player[this.total1].hp <= 0) {
        this.Result = "Monster win";
        this.Result2 = "Hero Death";
      }
    },
    sumMS(Attack4) {
      this.monster[this.total2].hp = this.monster[this.total2].hp - Attack4;
      if (this.monster[this.total2].hp <= 0) {
        this.Result = "Monster Death";
        this.Result2 = "Hero win";
      }
    },
  },
};
</script>

