<template>
  <div class="row-2">
    <div class="col">
      <button
        @click="randomDamageN(3,10),randomDamageMN(3,10)"
        class="btn btn-info"
      >กดเพื่อโจมตีธรรมดา</button>
    </div>
    <p></p>
    <div class="col">
      <button
        @click="randomDamageS(10,20),randomDamageMS(10,20)"
        class="btn btn-info"
      >กดเพื่อโจมตีท่าไม้ตาย</button>
    </div>
  </div>
</template>

<script>
export default {
  data: function () {
    return {
      numN: 0,
      numMN: 0,
      numS: 0,
      numMS: 0,
    };
  },
  methods: {
    randomDamageN: function (min, max) {
      this.numN = Math.max(Math.floor(Math.random() * max) + 1, min);
      this.$emit("N", this.numN);
    },
    randomDamageMN: function (min, max) {
      this.numMN = Math.max(Math.floor(Math.random() * max) + 1, min);
      this.$emit("MN", this.numMN);
    },
    randomDamageS: function (min, max) {
      this.numS = Math.max(Math.floor(Math.random() * max) + 1, min);
      this.$emit("S", this.numS);
    },
    randomDamageMS: function (min, max) {
      this.numMS = Math.max(Math.floor(Math.random() * max) + 1, min);
      this.$emit("MS", this.numMS);
    },
  },
};
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>